___CSS_LOADER_EXPORT___.push([module.id, `/*Estilos generales del proyecto*/
body {
    font-family: Arial, sans-serif;
    background-color:#f4f4f4;
    margin: 0; 
    padding: 0;
    height: 100vh;  
    display: flex;
    justify-content: center; 
    align-items: center; 

}

/*Estilos para el contenedor parcial */
#app {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

}

/*Estilos al formulario */
form {
    display: flex;
    justify-content: space-between;
}

/*Estilos al impunt */
input {
    width: 75%;
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ddd;
    border-radius: 3px;
}

/*Estilos de boton */
button {
    padding: 5 px 8px;
    background-color: #007bff;
    color: white;
    border:none;
    border-radius: 3px;
    cursor:pointer;
}

/*Estilos de la lista */
ul {
    list-style: none;
    padding: 0;
}

/*Estilos a los elementos de la lista */
li {
    background-color: #f4f4f4;
    padding: 10px;
    border: 1px solid #ddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


/*Estilos para cuando una tarea este completa */
li.completed {
    text-decoration:line-through;
    color: #888;
}



